.header {
  float: left;
  width: 15vw;
  height: 100%;
  background: #f2f5fd;
}

h1 {
  text-align: center;
  color: #4a69bd;
}

nav {
  float: none;
}

a {
  display: block;
  padding: 10px;
  border-left: 3px solid rgba(0, 0, 0, 0);
  text-decoration: none;
  color: rgba(0, 0, 0, 0.1);
}

a:hover,
a:active {
  background: rgba(255, 255, 255, 0.3);
}
.wow {
  color: #4a69bd;
  border-left: 3px solid #4a69bd;
}



