.buttonHolder {
  display: inline-block;
  margin-left: 40px;
}

.headerHolder {
  display: flex;
  margin-bottom: 20px;
}

.home {
  padding: 56px 40px;
  min-height: 100%;
  width: 85vw;
}

h1 {
  font-weight: normal;
  color: #4a69bd;
  padding: 0;
  margin: 0;
}
th {
  font-weight: normal;
  color: rgba(0, 0, 0, 0.2);
  font-size: 12px;
  text-align: left;
  padding-bottom: 10px;
}
table {
  min-width: 100%;
}



