.button {
  border-radius: 20px;
  width: 40px;
  height: 40px;
  border: none;
  box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.1);
  position: relative;
  margin-left: 5px;
  margin-right: 5px;
  cursor: pointer;
}

.button:hover svg {
  fill: rgba(0, 0, 0, 0.1);
}

.icon {
  width: 20px;
  height: 20px;
  position: absolute; /*it can be fixed too*/
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}



