.open {
  height: 30px;
  position: relative;
  width: 200px;
  border-radius: 15px;

  background: white;
  -webkit-transition: width 0.2s linear;
  -moz-transition: width 0.2s linear;
  -o-transition: width 0.2s linear;
  transition: width 0.2s linear;
  box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.1);
}
.red{
  background:red;
  height:20px;
  width:20px;
}
.border{
  border:1px solid blue;
}


.closed {
  height: 30px;
  position: relative;
  width: 30px;
  border-radius: 15px;

  -webkit-transition: width 0.2s linear;
  -moz-transition: width 0.2s linear;
  -o-transition: width 0.2s linear;
  transition: width 0.2s linear;
  box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.1);
}

.buttonO {
  width: 30px;
  height: 30px;
  position: absolute;
  background: green;
  animation: open 0.2s linear;
  transform: translate(170px);
  border-radius: 50%;
}

.buttonC {
  width: 30px;
  height: 30px;
  position: absolute;
  background: green;
  animation: close 0.2s linear;
  transform: translate(0px);
  border-radius: 50%;
}

.textfieldC {
  border: none;
  background: transparent;
  position: absolute;
  animation: opacityOut 0.2s linear;
}

.textfieldO {
  border: none;
  background: transparent;
  position: absolute;
  animation: opacity 0.2s linear;
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes opacityOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes close {
  0% {
    transform: translate(170px);
  }
  100% {
    transform: translate(0px);
  }
}

@keyframes open {
  0% {
    transform: translate(0px);
  }
  100% {
    transform: translate(170px);
  }
}



